.main-container{
  padding: 20px;
}

nav,
.nav-links {
  display: flex;
}

nav {
  justify-content: space-around;
  align-items: center;
  height: 17vh;
}

.nav-links {
  gap: 2rem;
  list-style: none;
  font-size: 1.5rem;
}

@media screen and (max-width:768px) {
  .nav-links {
      gap: 1rem;
      list-style: none;
      font-size: 0.8rem;
    }
}

a {
  color: black;
  text-decoration: none;
  text-decoration-color: white;
}

a:hover {
  color: grey;
  text-decoration: underline;
  
}

.logo {
  font-size: 2rem;
}

@media screen and (max-width:768px) {
  .logo {
      font-size: 0.8rem;
    }
}